import React, { useEffect, useState } from "react";
import axios from "axios";

// Асинхронная функция перевода
const doTranslation = async (input, languageCode, cancelToken) => {
  try {
    const y = 'https://dictionary.yandex.net/api/v1/dicservice.json';
    const url = `${y}/lookup?key=dict.1.1.20241130T115834Z.d9ddf7a36d0ef05c.69ad2d126339359e81fcae9cebeb91555a4fa682&lang=ru-${languageCode}&text=${input}`;
    
    // Отправка GET-запроса с отменой через CancelToken
    const { data } = await axios.get(url, { cancelToken: cancelToken.token });

    // Возврат перевода, если данные найдены
    return data.def[0].tr[0].text;
  } catch (err) {
    console.error(err);
    return "Перевод не найден";
  }
};

// Компонент
export default ({ language, text }) => {
  const [translated, setTranslated] = useState("");

  useEffect(() => {
    if (!text) {
      return;
    }

    // Создание CancelToken для отмены запросов
    const cancelToken = axios.CancelToken.source();

    // Выполнение перевода
    doTranslation(text, language, cancelToken).then(setTranslated);

    return () => {
      try {
        cancelToken.cancel();
      } catch (err) {
        console.error(err);
      }
    };
  }, [text, language]);

  return (
    <div>
      <label className="label">Выход</label>
      <h1 className="title">{translated}</h1>
    </div>
  );
};
